"use client";
import { useEffect, useRef } from "react";

export const BackgroundBeams = () => {
  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext("2d");
    if (!ctx) return;

    const setCanvasSize = () => {
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
    };

    setCanvasSize();

    class Meteor {
      x: number;
      y: number;
      length: number;
      speedX: number;
      speedY: number;
      size: number;
      opacity: number;

      constructor() {
        // Start from top-left area
        this.x = -100 - Math.random() * 200;
        this.y = -100 - Math.random() * 300;
        this.length = Math.random() * 200 + 100;

        // Diagonal movement (down-right direction)
        const speed = Math.random() * 8 + 6;
        const angle = Math.PI / 4 + (Math.random() - 0.5) * 0.3; // ~45° with slight variation
        this.speedX = Math.cos(angle) * speed;
        this.speedY = Math.sin(angle) * speed;

        this.size = Math.random() * 2.5 + 1.5;
        this.opacity = Math.random() * 0.7 + 0.3;
      }

      update() {
        this.x += this.speedX;
        this.y += this.speedY;

        // Reset when meteor goes off-screen
        if (this.x > canvas.width + 200 || this.y > canvas.height + 200) {
          this.x = -100 - Math.random() * 200;
          this.y = -100 - Math.random() * 300;
        }
      }

      draw() {
        ctx.save();

        // Calculate trail end point
        const endX = this.x - this.speedX * 15;
        const endY = this.y - this.speedY * 15;

        // Create gradient for meteor trail
        const gradient = ctx.createLinearGradient(this.x, this.y, endX, endY);

        // White hot head fading to emerald tail
        gradient.addColorStop(0, `rgba(255, 255, 255, ${this.opacity})`);
        gradient.addColorStop(0.05, `rgba(200, 255, 230, ${this.opacity * 0.95})`);
        gradient.addColorStop(0.15, `rgba(52, 211, 153, ${this.opacity * 0.85})`);
        gradient.addColorStop(0.35, `rgba(16, 185, 129, ${this.opacity * 0.6})`);
        gradient.addColorStop(0.65, `rgba(16, 185, 129, ${this.opacity * 0.3})`);
        gradient.addColorStop(1, `rgba(16, 185, 129, 0)`);

        // Draw meteor streak
        ctx.strokeStyle = gradient;
        ctx.lineWidth = this.size;
        ctx.lineCap = "round";

        ctx.beginPath();
        ctx.moveTo(this.x, this.y);
        ctx.lineTo(endX, endY);
        ctx.stroke();

        // Draw bright glowing head
        const headGradient = ctx.createRadialGradient(
          this.x,
          this.y,
          0,
          this.x,
          this.y,
          this.size * 4
        );
        headGradient.addColorStop(0, `rgba(255, 255, 255, ${this.opacity})`);
        headGradient.addColorStop(0.2, `rgba(200, 255, 230, ${this.opacity * 0.7})`);
        headGradient.addColorStop(0.5, `rgba(52, 211, 153, ${this.opacity * 0.4})`);
        headGradient.addColorStop(1, `rgba(16, 185, 129, 0)`);

        ctx.fillStyle = headGradient;
        ctx.beginPath();
        ctx.arc(this.x, this.y, this.size * 4, 0, Math.PI * 2);
        ctx.fill();

        // Add extra bright core
        ctx.fillStyle = `rgba(255, 255, 255, ${this.opacity * 0.9})`;
        ctx.beginPath();
        ctx.arc(this.x, this.y, this.size * 0.8, 0, Math.PI * 2);
        ctx.fill();

        ctx.restore();
      }
    }

    const meteors: Meteor[] = [];
    for (let i = 0; i < 30; i++) {
      meteors.push(new Meteor());
    }

    const animate = () => {
      ctx.clearRect(0, 0, canvas.width, canvas.height);

      meteors.forEach((meteor) => {
        meteor.update();
        meteor.draw();
      });

      requestAnimationFrame(animate);
    };

    animate();

    const handleResize = () => {
      setCanvasSize();
    };

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  return (
    <canvas
      ref={canvasRef}
      className="absolute inset-0 w-full h-full pointer-events-none"
      style={{
        zIndex: 1,
        opacity: 0.9,
      }}
    />
  );
};
